
html {
  background-color: black;
  font-family: Arial, Helvetica, sans-serif;
}
.timetable-container {
  width: 300px;
  height: 300px;
  margin: 0px auto 100px;
}
.timetable-control {
  color: #fff;
  width: 300px;
  margin: 0px auto;
}
.form-control input {
  border-radius: 3px;
  outline: none;
  border: 1px solid #ddd;
  padding: 5px 3px;
  font-size: 14px;
}
.form-control {
  margin: 15px 0;
}
h5 {
  color: #fff;
  margin: 10px 0 0;
}



